@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary: rgb(147, 189, 147);
  --primary-dark: rgb(131, 168, 131);
  --primary-letter: white;
  --light-gray: rgb(235, 235, 235);
  --gray: rgb(216, 216, 216);
  --black: rgb(100, 100, 100);
  --light: rgb(245, 242, 234);
}


* {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-weight: 200;
}


 a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}
.databar{
  padding: 50px 0;
  color: var(--primary-letter);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.databar-top, .databar-bottom{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.databar-bottom{
  margin-top: 50px;

}
.databar-data{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--primary-letter);
}
.databar-data h2{
  font-size: 40px;
  margin: 20px 0;
}
.databar-data h3{
  font-size: 25px;
  margin: 5px 0;
}

.progresscircle{
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.progresscircle-text{
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 150px;
  top: calc(50% - 75px);
  left: calc(50% - 150px);
}
.progresscircle-text h2{
  margin: 0;
  font-weight: 500;
}
.mealbar{
  box-shadow: 8px 8px var(--light-gray);
  border: 2px solid var(--gray);
  padding: 20px 30px;
  margin: 30px 0;
  transition: 0.1s all linear;
}
.mealbar:hover{
  box-shadow: 4px 4px var(--light-gray);
  transform: translate(4px,4px);
}

.mealbar-top {
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
}
.mealbar-emoji{
  font-size: 150px;
  margin: 0 20px 0 10px;
}
.mealbar-text{
  margin-left: 40px;
  min-width: 60vw;
}
.mealbar-text h2{
  font-size: 60px;
  margin: 0;
}
.mealbar-text h3{
  color: var(--black);
  margin: 10px 0 0 0;
  max-width: 400px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; 
}

.mealbar-top button{
  position:absolute;
  background-color: var(--light-gray);
  border-radius: 50%;
  padding: 10px 24px;
  font-size: 50px;
  right: 50px;
  top: 35%;
  transition: 0.1s all linear;
}
.mealbar-top button:hover{
  background-color: var(--gray);
}

.mealbar-bottom{
  display: flex;
  justify-content: center;
}
.mealbar-bottom h2{
  margin: 0;
}

.page-signin{
  background-color: var(--primary);
  margin: 0;
  min-height: 100vh;
  color: var(--primary-letter);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.page-signin h2{
  margin: 0;
}

.signin{
  display: flex;
  flex-direction: column;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 50px;
  align-items: center;
  border: 2px solid var(--light-gray);
  margin: 40px auto;
}

.signin h1{
  font-size: 90px;
  margin: 10px 0;
}

.signin input{
  max-width: 380px;
  font-size: 50px;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5%;
  border: 1px solid var(--primary-letter);
}

.signin button{
  font-size: 30px;
  border: 1px solid var(--primary-letter);
  background-color: var(--primary-letter);
  padding: 5px 50px;
  border-radius: 20%;
  color: var(--primary);
  margin-top: 20px;
}
.signin h4{
  margin: 15px 0 0 0;
}

#signin-btnOther{
  font-size: 20px;
  margin-top: 5px;
}
button{
  background-color: transparent;
  border: none;
  font-size: 30px;
}

.page-home{
  width: 100vw;
  min-height: 100vh;
}

.page-home-top{
  background-color: var(--primary);
  color: var(--primary-letter);
  padding: 15px;
  border-radius: 0 0 5% 5%;
}
.page-home-top button{
  color: var(--primary-letter);
  border: 1px solid var(--primary-letter);
}

.datechanger{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px 70px;
}

.page-home-mealbars{
  padding: 50px 60px;
}
.page-meal{
  background-color: var(--light);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.page-meal-top{
  background-color: var(--primary);
  color: white;
  padding: 80px 50px 60px 50px;
  border-radius: 0 0 5% 5%;
  width: 100%;
}

.page-meal-progress{
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.page-meal-top h2{
  font-size: 35px;
}

.page-meal-search-link{
  width: 100%;
  display:flex;
  justify-content: center;
}


.page-meal-datalist, .page-meal-foodlist{
  width: 100%;
}

.page-meal-back-div{
  position: absolute;
  top: 0;
  left: 50px;
  color: white;
  font-size: 40px;
}


.datalist-portion{
  padding: 30px 50px;
  display: flex;
  flex-direction: column;
  background-color: var(--primary-letter);
  width: 100%;
  margin: 30px 0;
}

.datalist h1{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 3em;
  font-weight: 500;
}

.datalist-info, .datalist-info-pie, .datalist-pie-label{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.datalist-info{
  justify-content: space-between;
}

.datalist-pie-info{
  width: 100%;
}

.datalist h2{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.datalist-pie{
  width: 200px;
}

.datalist-pie-color{
  width: 20px;
  height: 20px;
  background-color: rgb(147, 189, 147);
  border-radius: 50%;
  margin: 0 20px;
}




.page-search-top{
  background-color: var(--primary);
  color: white;
  display: flex;
  flex-direction: column;
  padding: 30px 50px 60px 50px;
}

.page-search-nav{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-search-nav h2{
  font-size: 40px;
}

.page-search-top button{
  color: white;
  font-size: 40px;
  margin-right: 20px;
}

.page-search-top input{
  outline: none;
  border: none;
  font-size: 40px;
  padding: 20px 50px;
  width: 80%;
  margin: 0 auto;
  border-radius: 40px;
}
.page-search-top input:focus{
  outline: none;
  border: none;
}

.page-search-items{
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  align-items: center;
}
.foodbar{
  width: 85%;
  box-shadow: 8px 8px var(--light-gray);
  border: 2px solid var(--gray);
  padding: 20px 40px;
  margin: 30px 0;
  transition: 0.1s all linear;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.foodbar:hover{
  box-shadow: 4px 4px var(--light-gray);
  transform: translate(4px,4px);
}

.foodbar-text{
  display: flex;
  flex-direction: column;
}

.foodbar-text h2{
  font-size: 40px;
  font-weight: 500;
}
.foodbar-text h3{
  font-size: 30px;
}

.foodbar button{
  background-color: var(--light-gray);
  border-radius: 50%;
  padding: 10px 24px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 50px;
  right: 50px;
  top: 35%;
  transition: 0.1s all linear;
}
.foodbar button:hover{
  background-color: var(--gray);
}

.foodbar img{
  width: 150px;
  height: 150px;
  background-size: cover;
  border-radius: 50%;
  margin-right: 30px;
}

.foodbar-left{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-food {
  background-color: var(--light);
  min-height: 100vh;
}

.page-food-top{
  background-color: var(--primary);
  height: 35vh;
  position: relative;
}
.page-food-top h2{
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: white;
  font-weight: 500;
  font-size: 60px;
  margin: 0;
}


.page-food-section{
  margin: 40px 30px;
  padding: 20px 70px;
  background-color: var(--primary-letter);
}
.page-food-section h2{
  font-size: 50px;
}
.page-food-section h3{
  font-size: 40px;
}

.page-food-inputs, .page-food-macros{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

#page-food-amount{
  border: none;
  border-bottom: 1px solid black;
  font-size: 55px;
  width: 200px;
}
#page-food-amount:focus{
  border: none;
  border-bottom: 1px solid black;
  outline: none;
}

/**/
.page-food-nutrients{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.page-food-nutrient {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 20px 0;
  border-bottom: 1px solid black;
}
.page-food-nutrient-top-text{
  font-size: 2em;
  margin: 10px 0;
}
.page-food-nutrient-top{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.page-food-nutrient-percent{
  font-size: 1.5em;
  margin: 0 auto;
}

/**/
.page-food-trackDiv{
  position: fixed;
  bottom: 0;
  width: 60%;
  left: calc(50vw - 30%);
}



